.timeline {
	margin-top: 7vh !important;
	position: relative;
	width: 100%;
	max-width: 1140px;
	margin: 0 auto;
	padding: 15px 0;
  }
  
  .timeline::after {
	content: '';
	position: absolute;
	width: 2px;
	background: $mainTextColor;
	top: 0;
	bottom: 0;
	left: 50%;
	margin-left: -1px;
  }
  
  .containerr {
	padding: 5px 30px;
	position: relative;
	background: inherit;
	width: 50%;
  }
  
  .containerr.left {
	right: 0;
  }
  
  .containerr.right {
	left: 50%;
  }
  
  .containerr::after {
	content: '';
	position: absolute;
	width: 16px;
	height: 16px;
	top: calc(50% - 8px);
	right: -8px;
	background: $bodyColor;
	border: 2px solid $mainTextColor;
	border-radius: 16px;
	z-index: 1;
  }
  
  .containerr.right::after {
	left: -8px;
  }
  
  .containerr::before {
	content: '';
	position: absolute;
	width: 23px;
	height: 2px;
	top: calc(50% - 1px);
	right: 8px;
	background-color: $mainTextColor;
	z-index: 1;
  }
  
  .containerr.right::before {
	left: 8px;
  }
  
  .containerr .machine{
	@include containerrClass();
	border: 1px solid $machineColor;

	h6,p{
		@include portfoliText();
		color: $machineColor;
	}
  }

  .containerr .nlp{
	@include containerrClass();
	border: 1px solid $staticColor;

	h6,p{
		@include portfoliText();
		color: $staticColor;
	}
  }

  .containerr .cv{
	@include containerrClass();
	border: 1px solid $deepColor;

	h6,p{
		@include portfoliText();
		color: $deepColor;
	}
  }
  

  .containerr .python{
	@include containerrClass();
	border: 1px solid $languageColor;

	h6,p{
		@include portfoliText();
		color: $languageColor;
	}
  }

  .containerr .database{
	@include containerrClass();
	border: 1px solid $databaseColor;

	h6,p{
		@include portfoliText();
		color: $databaseColor;
	}
  }

  .containerr .java{
	@include containerrClass();
	border: 1px solid $languageColorJ;

	h6,p{
		@include portfoliText();
		color: $languageColorJ;
	}
  }
  
  .containerr.right .content {
	padding: 30px 30px 30px 90px;
	border-radius: 500px 0 0 500px;
  }
  
  @media (max-width: 826px) {
	.timeline::after {
	  left: 120px;
	}
  
	.containerr {
	  width: 82.5% !important;
	  padding-left: 120px;
	  padding-right: 10px;
	}
  
	.containerr.right {
	  left: 3.7%;
	}

	.containerr.left {
		left: 3.7%;
	  }
  
	.containerr.left::after, 
	.containerr.right::after {
	  left: 84px;
	}
	.containerr.left::before,
	.containerr.right::before {
	  left: 100px;
	  border-color: transparent #006E51 transparent transparent;
	}
  
	.containerr.left .content,
	.containerr.right .content {
	  padding: 30px 30px 30px 90px;
	  border-radius: 500px 0 0 500px;
	}
  }

  @media (max-width: 700px) {
	.timeline::after {
	  display: none;
	}
  
	.containerr {
	  width: 100% !important;
	  padding-left: 30px;
	  padding-right: 10px;
	}
  
	.containerr.right {
	  left: -3%;
	}

	.containerr.left {
		left: -3%;
	  }
  
	h6, .content p, .github{
		font-size: $textFont !important;
	}

	.github{
		font-size: 1.3vh !important;
		font-weight: 700 !important;
	}

	.containerr.left::after, 
	.containerr.right::after {
	  display: none;
	}
  
	.containerr.left::before,
	.containerr.right::before {
	 display: none;
	}
  
	.containerr.left .content,
	.containerr.right .content {
	  padding: 10px 10px 10px 30px;
	}
  }
  
.portText{
	color:$textColor;
	font-family: $textFontFamily;
	font-size: $headerTextFontSize;
	margin-top: $textFont;
}

.div {
    display: inline-block;
    width: 17vh;
	margin: 1vh;
	font-size: $headerTextFontSize;
	margin-top: 2vh;
	font-family: $headerFontFamily;
	font-weight: bolder !important;

	.machine{
		color: $machineColor;
	}
	.backend{
		color:$deepColor;
	}

	.language{
		color:$languageColor;
	}
	.languageJ{
		color:$languageColorJ;
	}

	.database{
		color:$databaseColor;
	}
	.nlp{
		color: $staticColor;
	}
}

a{
	text-decoration: none;
	font-size: $headerTextFontSize;
	font-weight: 700;
	color: $mainTextColor;
	font-family: $headerFontFamily;
}

a:hover{
	text-decoration: none;
	color: $textColor;
}