$headerColor:#77868C;
$bodyColor: #f4f4f4;
$mainTextColor: #3E606F;
$cardColor: #566873;
$textColor: #193441;
$mainBackground: #F0F4F3;
$deepColor: #20B2AA;
$machineColor: #FFA07A;
$languageColor: #87CEFA;
$languageColorJ: #4bb14b;
$databaseColor: #FFB6C1;
$staticColor: #596877;
$headerFontFamily: 'Dancing Script';
$textFontFamily: 'Red Hat Display';
$mainFontFamily: 'Audiowide';
$headerTextFontSize: 2vh;
$textFont: 1.5vh;
$textFontSize: 5vh;
$mediaFontSize: 3.5vh;
$mainTextFontSize: 10vh;


@mixin flexColumn {
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
}

@mixin flexRow {
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
}

@mixin header {
    color:$mainTextColor;
    font-family: $textFontFamily;
    font-size: $textFontSize;
    margin-top: $textFontSize;
    text-transform: uppercase;
    @include flexRow();
}

@mixin middle{
    margin-left: auto;
    margin-right: auto;
}

@mixin middle1 {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin text {
    margin-top: $headerTextFontSize;
    @include middle();
    color:$textColor;
    font-family: $textFontFamily;
}

@mixin mainBox(){
    text-align: justify !important;
    font-family: $textFontFamily;
    background-color: $bodyColor;
}

@mixin containerrClass {
    width: 100%;
	padding: 30px 90px 30px 30px;
	position: relative;
	border-radius: 0 500px 500px 0;
	text-align: center;
}

@mixin portfoliText{
    margin: 0 0 10px 0;
	font-size: $headerTextFontSize;
	font-weight: normal;
	font-family: $mainFontFamily;
    line-height: 22px;
}
